<template>
    <header id="navbar" v-bind:style="{ top: barPos + 'px' }">
        <div id="navbar-content">
            
            <div id="mobile-burger" @click="() => { linkVisible = !linkVisible; }">
                <img src="../assets/burger.svg" v-bind:style="{ opacity: linkVisible === true ? '0' : '1' }">
                <img src="../assets/close.svg"  v-bind:style="{ opacity: linkVisible === true ? '1' : '0' }">
            </div>
            
            <router-link to="/" id="nav-logo">
                <img src="../assets/safear-logo.png">
            </router-link>
            
            <div id="nav-links" v-bind:class="{ 'link-visible': linkVisible}">
                <router-link to="/">Home</router-link>
                <router-link to="/store">Shop All</router-link>
                <router-link to="/about">About Us</router-link>
            </div>
            
            <div id="nav-tools">
                
                <img src="../assets/search.svg">
                <a href="/favs/">
                    <img src="../assets/fav.svg">
                </a>
                
                <Cart/>
            </div>
            
        </div>
    </header>
</template>

<script>
import Cart from '@/components/Cart.vue';

export default {
    name: 'NavBar',
    
    components: {
        Cart
    },
    
    created() {
        /* For hiding and showing the navigation bar */
        window.addEventListener('scroll', this.onscroll);
    },
    
    unmounted() {
        window.removeEventListener('scroll', this.onscroll);
    },
    
    data() {
        return {
            lastPos: 0,
            barPos: 0,
            
            /* Mobile only */
            linkVisible: false
        }
    },
    
    methods: {
        onscroll() {
            if(window.scrollY <= 70) {
                this.barPos = 0;
                return;
            }
            
            const scrollAmount = window.scrollY - this.lastPos;
            this.barPos -= scrollAmount;
            
            this.barPos = Math.min(0, Math.max(-70, this.barPos));
            
            this.lastPos = window.scrollY;
        }
    }
}
</script>

<style scoped>

/* Flexbox is bugged on firefox, please fix workaround */
#navbar
{
    padding: 16px;
    
    position: fixed;
    
    top: 0;
    width: 100%;
    height: 70px;
    
    background-color: white;
    box-shadow: 0 8px 20px var(--shadow-color);
    
    z-index: 999;
}

#navbar-content
{
    height: 100%;
    width: 100%;
    
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#navbar-content > *
{
    flex: 0 0 auto;    
}

/* The logo */
#nav-logo
{
    height: 100%;
}

#nav-logo > img 
{    
    height: 100%;
    width: auto;
    
    pointer-events: none;
    user-select: none;
}

/* The links */
#nav-links
{
    width: 1080px;
    height: 100%;
    
    display: flex;
    justify-content: space-between;
    align-items: center;    
}

#nav-links > a
{
    color: var(--font-color);
    font-weight: bold;
    
    text-decoration: none;
}

/* The tools (Shopping bag, favourites, etc...) */
#nav-tools
{
    height: 100%;
    
    padding: 4px 0;
    
    display: flex;
    justify-content: flex-end;
    
    gap: 16px;
}

/* The favs link */
#nav-tools > a 
{
    width: 30px;
}

#mobile-burger { display: none; }

/* Smaller screens and phones */
@media only screen and (max-width: 1000px)
{
    /* Setting the logos position in the middle */
    #nav-logo
    {
        position: absolute;
        
        height: 38px;
        
        left: 50%;
        transform: translate(-50%);
    }
    
    #nav-links
    {        
        position: absolute;
        
        top: 70px;
        left: 0;
        
        width: 100vw;
        height: calc(100vh - 70px);
        
        background-color: white;
        
        padding-left: 8px;
        
        flex-direction: column;
        justify-content: flex-start;
        gap: 8px;
        align-items: flex-start;
        
        transform: translateX(-100%);
        
        transition: transform .3s ease-in-out;
    }
    
    .link-visible
    {
        transform: translateX(0) !important;
    }
    
    #nav-tools
    {
        gap: 8px;
    }
    
    #mobile-burger
    {
        display: block;
        
        position: relative;
        
        padding: 4px 0;
    }
    
    #mobile-burger > img
    {
        position: absolute;
        height: 30px;
        transform: translateY(-50%);
        
        transition: opacity .1s ease-in-out;
    }
}

</style>