<template>
    <div>
        <img src="../assets/shoppingbag.svg" @click="open">
        <div v-if="isOpen" id="cart-content">
            <img id="cart-close" src="../assets/close.svg" @click="() => { isOpen = false; }">
            
            <p id="cart-title">Shopping Bag</p>
            
            <div id="cart-list">
                <div class="cart-item" v-for="index in 50" :key="index">
                    
                    <img class="cart-item-img" src="../assets/fallback.png">
                    
                    <div class="cart-item-desc">
                        <p>Name</p>
                        <div class="cart-item-amount"></div>
                        <p>$123</p>
                    </div>
                    
                    <img class="cart-item-close" src="../assets/close.svg">
                    
                </div>
            </div>
            <div id="cart-checkout">
                <p>4 Items</p>
                <button>Checkout</button>
            </div>
        </div>
    </div>
</template>

<script>
/* TODO: Hide on scroll (Not when scrolling on the cart-content) and when pressing on other objects on the page */

export default {
    name: 'Cart',
    
    created() {
        /* For hiding the cart-content */
        window.addEventListener('scroll', this.onscroll);
    },
    
    unmounted() {
        window.removeEventListener('scroll', this.onscroll);
    },
    
    data() {
        return {
            isOpen: true
        }
    },
    
    methods: {
        open() {
            this.isOpen = !this.isOpen;
        },
        
        onscroll() {
            this.isOpen = false;
        },
    }
}
</script>

<style scoped>

/* The icon */
img
{
    width: 100%;
    height: 100%;
    
    cursor: pointer;
}


/* The panel with the content */
#cart-content
{
    padding: 8px;
    
    width: 400px;
    max-height: 300px;
    
    position: absolute;
    top: 86px;
    right: 16px;
    
    box-shadow: 0 8px 20px rgb(47, 47, 49, 25%);
    
    background-color: white;
    
    display: flex;
    flex-direction: column;
}

/* The title on top of the content */
#cart-title
{
    font-weight: bold;
    font-size: 16px;
    
    margin-bottom: 8px;
}

#cart-close
{
    position: absolute;
    top: 10px;
    right: 10px;
    
    height: 16px;
    width: auto;
}

/* The list displaying the current items in the bag */
#cart-list
{
    overflow-x: scroll;
    
    width: 100%;
    min-height: 70px;
    
    display: flex;
    flex-direction: column;
        
    padding: 2px 0;
    gap: 2px;
    
    /* The line/border color */
    background-color: var(--background-color);
}

/* Removing the scroll bar to the cart-list */
#cart-list::-webkit-scrollbar { display: none; }

.cart-item
{    
    padding: 8px;
    
    flex: 0 0 auto;
    
    width: 100%;
    height: 70px;
    
    display: flex;
    justify-content: stretch;
    align-items: center;
    gap: 12px;
    
    border-radius: 4px;
    background-color: white;
}

.cart-item-img
{
    height: 100%;
    width: auto;
}

.cart-item-desc
{
    width: 100%;
    
    display: flex;
    justify-content: space-between;
}

.cart-item-close
{
    width: 16px;
    height: auto;
}


#cart-checkout
{
    margin-top: 8px;
    
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#cart-checkout > p
{
    font-weight: bold;
}

#cart-checkout > button
{
    cursor: pointer;
    
    padding: 8px 16px;
    font-size: 16px;
    
    border: none;
    
    background-color: black;
    color: white;
}

/* Smaller screens and phones */
@media only screen and (max-width: 1000px)
{
    #cart-content
    {
        right: 0;
        top: 70px;
        
        max-height: none;
        width: 100vw;
        height: calc(100vh - 70px);
    }
}

</style>